<template>
    <div class="m-publish-box">
        <!--  -->
        <publish-header name="">
            <slot name="header"></slot>
        </publish-header>

        <el-form class="m-publish-post">
            <div class="m-publish-source">
                <el-divider content-position="left"> *</el-divider>
                <el-select
                    class="u-source-id"
                    v-model="post.source_id"
                    filterable
                    remote
                    :disabled="!!post.id"
                    placeholder="///"
                    :remote-method="search_handle"
                    :loading="options.loading"
                >
                    <el-option v-for="item in options.sources" :key="item.ID" :label="item.Name" :value="item.ID">
                        <div class="m-selector-item">
                            <img class="u-icon" :src="icon_url_filter(item.IconID)" :alt="item.Name" />
                            <span class="u-name" v-text="item.Name"></span>
                        </div>
                    </el-option>
                </el-select>
            </div>

            <div class="m-publish-level">
                <el-divider content-position="left"> *</el-divider>
                <el-rate v-model="post.level" class="u-level"></el-rate>
            </div>

            <div class="m-publish-remark">
                <el-divider content-position="left"> *</el-divider>
                <el-input v-model="post.remark" :maxlength="200" :minlength="1" show-word-limit required placeholder=""></el-input>
            </div>

            <div class="m-publish-content">
                <el-divider content-position="left"> *</el-divider>
                <Tinymce v-model="post.content" :attachmentEnable="true" :resourceEnable="true" :height="400" />
            </div>

            <div class="m-publish-commit">
                <el-divider content-position="left"></el-divider>
                <el-button class="u-publish" icon="el-icon-s-promotion" type="primary" @click="toPublish" :disabled="processing"> </el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
import header from "@/components/publish_header.vue";
import Tinymce from "@jx3box/jx3box-editor/src/Tinymce";

// 
import { wiki } from "@jx3box/jx3box-common/js/wiki";
import User from "@jx3box/jx3box-common/js/user";
import { search_achievements } from "../service/achievement";
import { iconLink } from "@jx3box/jx3box-common/js/utils";
export default {
    name: "achievement",
    data() {
        return {
            // - 
            options: {
                sources: null,
            },

            // - 
            post: {
                id: "", // ID
                content: "",
                source_id: "",
                level: 0,
                remark: "",
            },

            // 
            loading: false,
            processing: false,
        };
    },
    computed : {
        client : function (){
            return this.$store.state.client
        }
    },
    methods: {
        toPublish: function() {
            if (!this.post.source_id) {
                this.$message({
                    message: "",
                    type: "warning",
                });
                return;
            }

            if (!this.post.content) {
                this.$message({ message: "", type: "warning" });
                return;
            }

            if (!(this.post.level >= 1 && this.post.level <= 5)) {
                this.$message({
                    message: "",
                    type: "warning",
                });
                return;
            }

            if (!this.post.remark) {
                this.$message({
                    message: "",
                    type: "warning",
                });
                return;
            }

            this.processing = true;
            const data = {
                source_id: String(this.post.source_id),
                level: this.post.level,
                user_nickname: User.getInfo().name,
                content: this.post.content,
                remark: this.post.remark,
            }
            wiki.post({ type: 'achievement', data: data, client: this.client }, {})
                .then((data) => {
                    data = data.data;
                    if (data.code === 200) {
                        this.$message({
                            message: "",
                            type: "success",
                        });
                        setTimeout(() => {
                            this.$router.push({ name: "wiki_post", params: { type: "achievement" } });
                        }, 500);
                    } else {
                        this.$message({
                            message: `${data.message}`,
                            type: "warning",
                        });
                    }
                })
                .finally(() => {
                    this.processing = false;
                });
        },
        icon_url_filter(icon_id) {
            return iconLink(icon_id);
        },
        // 
        search_handle(keyword = "") {
            this.loading = true;
            search_achievements({
                keyword: keyword,
                limit: 10,
            }).then((res) => {
                res = res.data;
                if (res.code === 200) this.options.sources = res.data.achievements;
                this.loading = false;
            });
        },
        loadData: function(client) {
            this.loading = true;
            // return WikiPost.newest("achievement", this.post.source_id, 0, client)
            return wiki.get({ type: 'achievement', id: this.post.source_id }, { supply: 0, client })
                .then((res) => {
                    let data = res.data;
                    // 
                    let post = data.data.post;
                    let achievement = data.data.source;

                    if (post) {
                        this.post.source_id = parseInt(post.source_id);
                        this.post.level = post.level || 1;
                        this.post.remark = "";
                        let content = post.content;
                        content = content.replace(/(<p>)?\s* []+\s*(<\/p>)?/gi, "");
                        content = content.replace(/(<p>)?\s* []+\s*(<\/p>)?/gi, "");
                        content = content.replace(/(<p>)?\s*\s*(<\/p>)?/gi, "");
                        this.post.content = content;
                    } else {
                        this.post.source_id = this.post.source_id ? parseInt(this.post.source_id) : "";
                        this.post.level = 0;
                        this.post.remark = "";
                        this.post.content = "";
                    }

                    if (achievement) {
                        // 
                        let exist = false;
                        this.options.sources = this.options.sources || [];
                        for (let index in this.options.sources) {
                            if (this.options.sources[index].ID == achievement.ID) {
                                exist = true;
                                break;
                            }
                        }
                        if (!exist) this.options.sources.push(achievement);
                    }

                    return post;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
    },
    created() {
        // 
        this.search_handle("");
        // IDwatch
        let id = this.$route.params.achievement_id;
        this.post.source_id = id ? parseInt(id) : null;
    },
    watch: {
        "post.source_id": {
            handler: function(val) {
                if (!val) return;

                if(this.client == 'std'){
                    this.loadData('std')
                }else{
                    this.loadData('origin').then((post) => {
                        console.log('')
                        if(!post){
                            this.loadData('std')
                        }
                    })
                }
            },
        },
    },
    components: {
        "publish-header": header,
        Tinymce,
    },
};
</script>

<style scoped lang="less">
@import "../assets/css/achievement.less";
</style>
